import { Component, OnInit, OnDestroy } from '@angular/core';
import { JeuVideo } from 'src/app/services/interfaces/jeu-video';
import { DataJeuVideoService } from 'src/app/services/data-jeu-video.service';
import { NavController } from '@ionic/angular';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-jeux-video',
	templateUrl: './jeux-video.page.html',
	styleUrls: [ './jeux-video.page.scss' ]
})
export class JeuxVideoPage implements OnInit, OnDestroy {
	public mesJeuxVideo: JeuVideo[];
	public searchInput: string;

	private jeuVideoSub: Subscription;

	constructor(private navCtrl: NavController, private jeuVideoService: DataJeuVideoService) {
		this.mesJeuxVideo = [ ...jeuVideoService.myJeuVideo ];
	}

	ngOnInit() {
		this.jeuVideoSub = this.jeuVideoService.myJeuVideoChanged.subscribe((jv) => {
			this.mesJeuxVideo = jv;
		});
	}

	ngOnDestroy() {
		this.jeuVideoSub.unsubscribe();
	}

	goToAjouter() {
		this.navCtrl.navigateForward([ 'selectionner-jeu-video' ]);
	}

	showInfo(id: number) {
		this.navCtrl.navigateForward([ '/info-jeu-video/' + id ]);
	}
}
