import { Component, OnInit } from '@angular/core';
import { JeuVideo } from 'src/app/services/interfaces/jeu-video';
import { Router } from '@angular/router';
import { DataJeuVideoService } from 'src/app/services/data-jeu-video.service';
import { AlertController } from '@ionic/angular';

@Component({
	selector: 'app-jeux-video',
	templateUrl: './jeux-video.page.html',
	styleUrls: [ './jeux-video.page.scss' ]
})
export class JeuxVideoPage implements OnInit {
	public searchInput: string;
	public jeuxVideoRec: JeuVideo[];

	constructor(
		private router: Router,
		private jeuVideoService: DataJeuVideoService,
		private alertCtrl: AlertController
	) {
		this.jeuxVideoRec = [ ...jeuVideoService.recJeuVideo ];
	}

	ngOnInit() {
		this.jeuxVideoRec = this.shuffle(this.jeuxVideoRec);
	}

	shuffle(array) {
		var currentIndex = array.length,
			temporaryValue,
			randomIndex;
		while (0 !== currentIndex) {
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex -= 1;
			temporaryValue = array[currentIndex];
			array[currentIndex] = array[randomIndex];
			array[randomIndex] = temporaryValue;
		}
		return array;
	}

	async supprimer(jv: JeuVideo) {
		const alert = await this.alertCtrl.create({
			header: 'Suprimer un Jeu Vidéo',
			message: 'Voulez vous vraiment supprimer ce jeu vidéo de vos recommandations ?',
			buttons: [
				'Annuler',
				{
					text: 'Valider',
					role: 'valider',
					handler: () => {
						var i: number = this.jeuxVideoRec.indexOf(jv);
						this.jeuxVideoRec.splice(i, 1);
						this.jeuVideoService.delInRecJeuVideo(jv);
					}
				}
			]
		});

		await alert.present();
	}
}
